import React, { Component } from 'react';
import { Link, NavLink } from 'react-router-dom';
import { Badge, Modal, ModalBody, ModalHeader, UncontrolledDropdown, DropdownItem, DropdownMenu, DropdownToggle, Nav, NavItem, ButtonDropdown } from 'reactstrap';
import PropTypes from 'prop-types';

import { AppAsideToggler, AppNavbarBrand, AppSidebarToggler } from '@coreui/react';
import logo from '../../assets/img/brand/logo.svg'
import sygnet from '../../assets/img/brand/sygnet.svg'
import Cookies from 'universal-cookie';
const cookies = new Cookies();

const propTypes = {
  children: PropTypes.node,
};

const defaultProps = {};

class DefaultHeader extends Component {
  constructor(props) {
    super(props);
    this.state = {
      admin: '',
      contactsOpen: false,
      forumOpen: false,
      surveyOpen: false,
      careerOpen: false,
      modalOpen: false,
    };
    this.toggleModal = this.toggleModal.bind(this);
    this.toggleContacts = this.toggleContacts.bind(this);
    this.toggleForum = this.toggleForum.bind(this);
    this.toggleSurvey = this.toggleSurvey.bind(this);
    this.toggleCareer = this.toggleCareer.bind(this);
  }

  toggleModal() {
    this.setState({
      modalOpen: !this.state.modalOpen,
    });
  }

  componentWillMount() {
    setTimeout(() => {
      this.setState({
        admin: cookies.get('admin')
      })
    }, 2000);
  }

  toggleContacts() {
    this.setState({
      contactsOpen: !this.state.contactsOpen
    });
  }

  toggleForum() {
    this.setState({
      forumOpen: !this.state.forumOpen
    });
  }

  toggleSurvey() {
    this.setState({
      surveyOpen: !this.state.surveyOpen
    });
  }

  toggleCareer() {
    this.setState({
      careerOpen: !this.state.careerOpen
    });
  }

  render() {

    // eslint-disable-next-line
    const { children, ...attributes } = this.props;

    return (
      <React.Fragment>
        <div style={{ display: 'flex', width: '100%' }}>
          <div style={{ flex: 1 }}>
            <AppSidebarToggler className="d-lg-none" display="md" mobile />
            <AppNavbarBrand
              full={{ src: logo, width: 89, height: 25, alt: 'CoreUI Logo' }}
              minimized={{ src: sygnet, width: 30, height: 30, alt: 'CoreUI Logo' }}
            />
          </div>
          {/* <AppSidebarToggler className="d-md-down-none" display="lg" /> */}
          <div style={{ flex: 8 }}>
            <Nav className="d-md-down-none" style={{ float: 'right', height: '-webkit-fill-available' }} navbar>
              {/* <NavItem className="px-3">
            <NavLink to="/dashboard" className="nav-link" >Dashboard</NavLink>
          </NavItem>
          <NavItem className="px-3">
            <Link to="/users" className="nav-link">Users</Link>
          </NavItem>
          <NavItem className="px-3">
            <NavLink to="#" className="nav-link">Settings</NavLink>
          </NavItem> */}

              <NavItem className="px-3">
                <NavLink to="/dashboard" className="nav-link" style={{ color: 'white' }} >Home</NavLink>
              </NavItem>

              <ButtonDropdown isOpen={this.state.contactsOpen} toggle={this.toggleContacts}>
                <DropdownToggle style={{ backgroundColor: '#0000A0', border: 'none', color: 'white' }} caret>
                  Contacts
            </DropdownToggle>
                <DropdownMenu>
                  <DropdownItem>
                    <NavItem className="px-3">
                      <NavLink to="/about" className="nav-link" >About Us</NavLink>
                    </NavItem>
                  </DropdownItem>
                  <DropdownItem>
                    <NavItem className="px-3">
                      <NavLink to="/values" className="nav-link" >Our Values</NavLink>
                    </NavItem>
                  </DropdownItem>
                </DropdownMenu>
              </ButtonDropdown>

              <ButtonDropdown isOpen={this.state.forumOpen} toggle={this.toggleForum}>
                <DropdownToggle style={{ backgroundColor: '#0000A0', border: 'none', color: 'white' }} caret>
                  Online Forum
            </DropdownToggle>
                <DropdownMenu>
                  {this.state.admin !== 'true' ?
                    <DropdownItem>
                      <NavItem className="px-3">
                        <NavLink to="/forum/create-question" className="nav-link" >Create Question</NavLink>
                      </NavItem>
                    </DropdownItem>
                    : null}
                  <DropdownItem>
                    <NavItem className="px-3">
                      <NavLink to="/forum/question-list" className="nav-link" >Question List</NavLink>
                    </NavItem>
                  </DropdownItem>
                </DropdownMenu>
              </ButtonDropdown>

              <ButtonDropdown isOpen={this.state.surveyOpen} toggle={this.toggleSurvey}>
                <DropdownToggle style={{ backgroundColor: '#0000A0', border: 'none', color: 'white' }} caret>
                  Online Survey
            </DropdownToggle>
                <DropdownMenu>
                  <DropdownItem>
                    <NavItem className="px-3">
                      <NavLink to="/survey/financial" className="nav-link" >Financial Opportunities</NavLink>
                    </NavItem>
                  </DropdownItem>
                  {this.state.admin == 'false' ?
                    <DropdownItem>
                      <NavItem className="px-3">
                        <NavLink to="/survey/survey" className="nav-link" >Online Survey</NavLink>
                      </NavItem>
                    </DropdownItem>
                    : null}
                  {this.state.admin !== 'false' ?
                    <DropdownItem>
                      <NavItem className="px-3">
                        <NavLink to="/survey/chatlist" className="nav-link" >Chat List</NavLink>
                      </NavItem>
                    </DropdownItem>
                    : null}
                  {this.state.admin !== 'false' ?
                    <DropdownItem>
                      <NavItem className="px-3">
                        <NavLink to="/survey/surveylist" className="nav-link" >Survey List</NavLink>
                      </NavItem>
                    </DropdownItem>
                    : null}
                </DropdownMenu>
              </ButtonDropdown>

              <ButtonDropdown isOpen={this.state.careerOpen} toggle={this.toggleCareer}>
                <DropdownToggle style={{ backgroundColor: '#0000A0', border: 'none', color: 'white' }} caret>
                  Careers
            </DropdownToggle>
                <DropdownMenu>
                  <DropdownItem>
                    <NavItem className="px-3">
                      <NavLink to="/careers/portal" className="nav-link" >Support Portal</NavLink>
                    </NavItem>
                  </DropdownItem>
                </DropdownMenu>
              </ButtonDropdown>

              <NavItem className="px-3">
                <a onClick={this.toggleModal} style={{ cursor: 'pointer' }}><div style={{ textAlign: 'center', height: '30px', color: '#ff6363' }}>Emergency Contact</div></a>
              </NavItem>

            </Nav>
          </div>
          <div style={{ flex: 1 }}>
            <Nav className="ml-auto" style={{ height: '-webkit-fill-available' }} navbar>
              {/* <NavItem className="d-md-down-none">
            <NavLink to="#" className="nav-link"><i className="icon-bell"></i><Badge pill color="danger">5</Badge></NavLink>
          </NavItem>
          <NavItem className="d-md-down-none">
            <NavLink to="#" className="nav-link"><i className="icon-list"></i></NavLink>
          </NavItem>
          <NavItem className="d-md-down-none">
            <NavLink to="#" className="nav-link"><i className="icon-location-pin"></i></NavLink>
          </NavItem> */}
              {this.state.admin !== 'false' ? <h6 style={{ color: 'white' }}>{this.state.admin}</h6> : null}
              <UncontrolledDropdown nav direction="down">
                <DropdownToggle nav>
                  <img src={'assets/img/avatars/girl.jpg'} className="img-avatar" alt="admin@bootstrapmaster.com" />
                </DropdownToggle>
                <DropdownMenu right>
                  {/* <DropdownItem header tag="div" className="text-center"><strong>Account</strong></DropdownItem>
              <DropdownItem><i className="fa fa-bell-o"></i> Updates<Badge color="info">42</Badge></DropdownItem>
              <DropdownItem><i className="fa fa-envelope-o"></i> Messages<Badge color="success">42</Badge></DropdownItem>
              <DropdownItem><i className="fa fa-tasks"></i> Tasks<Badge color="danger">42</Badge></DropdownItem>
              <DropdownItem><i className="fa fa-comments"></i> Comments<Badge color="warning">42</Badge></DropdownItem>
              <DropdownItem header tag="div" className="text-center"><strong>Settings</strong></DropdownItem>
              <DropdownItem><i className="fa fa-user"></i> Profile</DropdownItem>
              <DropdownItem><i className="fa fa-wrench"></i> Settings</DropdownItem>
              <DropdownItem><i className="fa fa-usd"></i> Payments<Badge color="secondary">42</Badge></DropdownItem>
              <DropdownItem><i className="fa fa-file"></i> Projects<Badge color="primary">42</Badge></DropdownItem>
              <DropdownItem divider />
              <DropdownItem><i className="fa fa-shield"></i> Lock Account</DropdownItem> */}
                  <DropdownItem onClick={e => this.props.onLogout(e)}><i className="fa fa-lock"></i> Logout</DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </Nav>
          </div>
        </div>
        <Modal isOpen={this.state.modalOpen} toggle={this.toggleModal}
          className={'modal-primary ' + this.props.className}>
          <ModalHeader>Emergency Contact</ModalHeader>
          <ModalBody>
            <table style={{ width: '100%' }}>
              <tbody>
                <tr>
                  <td><b>Ambulance/ Fire/ Police</b></td>
                  <td></td>
                  <td><a href="tel:000">000</a></td>
                </tr>
                <tr>
                  <td>&nbsp;</td>
                </tr>
                <tr>
                  <td><b>Lifeline,24hr counselling</b></td>
                  <td><a href="http://www.lifeline.org.au" target="_blank">www.lifeline.org.au</a></td>
                  <td><a href="tel:131114">131114</a></td>
                </tr>
                <tr>
                  <td>&nbsp;</td>
                </tr>
                <tr>
                  <td><b>Suicide Call Back Service</b></td>
                  <td><a href="http://www.suicidecallbackservice.org.au" target="_blank">www.suicidecallbackservice.org.au</a></td>
                  <td style={{ width: '100px' }}><a href="tel:1300 659 467">1300 659 467</a></td>
                </tr>
                <tr>
                  <td>&nbsp;</td>
                </tr>
                <tr>
                  <td><b>1800 RESPECT (Domestic Violence   Support)</b></td>
                  <td><a href="http://www.1800respect.org.au" target="_blank">www.1800respect.org.au</a></td>
                  <td><a href="tel:1800 737 732">1800 737 732</a></td>
                </tr>
              </tbody>
            </table>
          </ModalBody>
        </Modal>
        {/* <AppAsideToggler className="d-md-down-none" /> */}
        {/*<AppAsideToggler className="d-lg-none" mobile />*/}
      </React.Fragment>
    );
  }
}

DefaultHeader.propTypes = propTypes;
DefaultHeader.defaultProps = defaultProps;

export default DefaultHeader;
